import React, { Component } from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';
import Projects from './components/Projects';
import Home from './components/Home';
import Bubbles from './components/Bubbles';
import Contact from './components/Contact';
import About from './components/About';

class App extends Component {
  state = {
    color: '',
    active: 0
  };

  componentDidMount() {
    const colors = ['rgba(4, 125, 146, 0.603)', 'rgba(146, 37, 4, 0.603)', 'rgba(4, 146, 16, 0.603)', 'rgba(146, 132, 4, 0.603)', 'rgba(146, 68, 4, 0.603)', 'rgba(2, 90, 2, 0.8)', 'rgba(146, 4, 4, 0.603)', 'rgba(101, 4, 146, 0.603)', 'rgba(146, 4, 92, 0.603)'];
    const random = Math.floor((Math.random() * (colors.length - 1)) + 0);
    this.setState({
      color: colors[random]
    });
  };

  changeLinkColor(value) {
    this.setState({
      active: value
    });
  };

  render() {
    const { color, active } = this.state;
    return (
      <Router>
        <div className="App" style={{ '--all-color': color }}>
          <Bubbles />
          <div className="navbar">
            <nav className="navbar__section navbar__section--right">
              <div className="navbar__item">
                <Link className="navbar__link" to="/" style={active === 0 ? { color: color } : null}>Home</Link>
              </div>
              <div className="navbar__item">
                <Link className="navbar__link" to="/about" style={active === 1 ? { color: color } : null}>About</Link>
              </div>
              <div className="navbar__item">
                <Link className="navbar__link" to="/projects" style={active === 2 ? { color: color } : null}>Projects</Link>
              </div>
              <div className="navbar__item">
                <Link className="navbar__link" to="/contact" style={active === 3 ? { color: color } : null}>Contact</Link>
              </div>
            </nav>
            <Route exact={true} path="/" render={(props) => <Home {...props} active={this.changeLinkColor.bind(this)} />} />
            <Route path="/about" render={(props) => <About {...props} active={this.changeLinkColor.bind(this)} />} />
            <Route path="/projects" render={(props) => <Projects {...props} active={this.changeLinkColor.bind(this)} />} />
            <Route path="/contact" render={(props) => <Contact {...props} active={this.changeLinkColor.bind(this)} />} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
